import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import FontAwesome from 'react-fontawesome';
import { compose, withProps } from 'recompose';
import {
  withScriptjs,
  withGoogleMap,
  GoogleMap,
  Marker,
} from 'react-google-maps';
import Breadcrumb from '../../components/Breadcrumb';
import s from './ContactUs.css';
import Link from '../../components/Link';

const MyMapComponent = compose(
  withProps({
    googleMapURL:
      'https://maps.googleapis.com/maps/api/js?key=AIzaSyDeLThqYG0AvcdD_jJFFFvgZoZoGauRQeg&v=3.exp&libraries=geometry,drawing,places',
    loadingElement: <div style={{ height: `100%` }} />,
    containerElement: <div style={{ height: `400px` }} />,
    mapElement: <div style={{ height: `100%` }} />,
  }),
  withScriptjs,
  withGoogleMap,
)(props =>
  <GoogleMap
    defaultZoom={15}
    defaultCenter={{ lat: 1.7535314, lng: 103.4090493 }}
  >
    {props.isMarkerShown &&
      <Marker
        position={{ lat: 1.7535314, lng: 103.4090493 }}
        onClick={props.onMarkerClick}
      />}
  </GoogleMap>,
);

class ContactUs extends React.Component {
  static propTypes = {
    title: PropTypes.string.isRequired,
  };

  state = {
    isMarkerShown: false,
  };

  componentDidMount() {
    this.delayedShowMarker();
  }

  delayedShowMarker = () => {
    setTimeout(() => {
      this.setState({ isMarkerShown: true });
    }, 3000);
  };

  handleMarkerClick = () => {
    this.setState({ isMarkerShown: false });
    this.delayedShowMarker();
  };

  render() {
    return (
      <div className={s.root}>
        <Breadcrumb title={this.props.title} />
        <div className={s.container}>
          <div className={s.contents}>
            <div className={s.map}>
              <MyMapComponent
                isMarkerShown={this.state.isMarkerShown}
                onMarkerClick={this.handleMarkerClick}
              />
            </div>
            <div className={s.contact}>
              <h1>Maybricks Sdn Bhd</h1>
              <ul className="fa-ul">
                <li>
                  <FontAwesome className="fa-li" name="map-marker" />Batu 36,
                  Jalan Air Hitam, Bukit Batu<br />81000 Kulaijaya, Johor,
                  Malaysia.
                </li>
                <li>
                  <FontAwesome className="fa-li" name="phone" />
                  <Link to="tel:+6076511022">(60) 7 651 1022</Link>,&nbsp;
                  <Link to="tel:+6076511186">651 1186</Link>,&nbsp;
                  <Link to="tel:+6076512210">651 2210</Link>
                </li>
                <li>
                  <FontAwesome className="fa-li" name="fax" />
                  <Link to="tel:+6076512818">(60) 7 651 2818</Link>
                </li>
                <li>
                  <FontAwesome className="fa-li" name="envelope" />
                  <a href="mailto:maybrickskulai@gmail.com">
                    maybrickskulai@gmail.com
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <hr />
          <form>
            <h1>Send us a message</h1>
            <div>
              <input
                type="text"
                name="name"
                required
                placeholder="Your name (required)"
              />
              <input type="text" name="phone" placeholder="Your phone" />
              <input
                type="text"
                name="email"
                required
                placeholder="Your email (required)"
              />
            </div>
            <textarea
              name="message"
              cols="40"
              rows="10"
              required
              placeholder="Your message (required)"
            />
            <input type="submit" value="Submit" />
          </form>
        </div>
      </div>
    );
  }
}

export default withStyles(s)(ContactUs);



