var _ref2 = _jsx('p', {}, void 0, 'The manufacturing of brick begins with the gathering of surface clays and shales from the quarry. Raw materials are transported to storage areas in preparation for crushing. The crusher breaks up the large chunks of clay or shale and passes it to conveyors heading for the grinders. The grinders or \u2018Muller Wheels\u2019 pulverize the material to a fine consistency. The pulverized material passes over a vibrating screen in which the fine material passes through to the next step and the coarse material is returned to the grinder to be reground');

var _ref3 = _jsx('p', {}, void 0, 'Up to this point the material has been kept dry. Once screened, the material is sent to the pug mill where it will be tempered to make a homogeneous plastic mass ready for shaping in the required brick form. The pug mill consists of a chamber that houses one or two revolving shafts with blades rigidly attached, which thoroughly mix the material. Water is added to provide the proper plasticity and materials, such as manganese, are added to change the body color.');

var _ref4 = _jsx('p', {}, void 0, 'Clay preparation methods may have to accommodate the physical characteristics of the raw material and special provision may have to be made to deal with certain impurities.');

var _ref5 = _jsx('p', {}, void 0, 'Preparation consists of transforming the clay rock into plastic mouldable material by a process of grinding and mixing with water. A typical factory might have a Primary crusher, these are used to break down large lumps of rock to manageable size, which can then be fed to a Secondary crusher, for example Pan mill, where the clay is reduced in size further. Water can be added here or if it is a dry pan the clay is reduced to dust and water added later. Further crushing takes place through conveyor rollers reducing the clay particles to about 1-2mm.');

var _ref6 = _jsx('h2', {}, void 0, 'Brick Making \u2013 Extrusion');

var _ref7 = _jsx('p', {}, void 0, 'The clay body is mixed to a fairly stiff texture and is then loaded into an extruder where a worm screw pushes it along a barrel into a vacuum chamber which compresses it through a taper and out through a die. The die is machined to a precise size and shape larger than the finished size of the brick, calculating how much the clay will shrink during the drying and firing process. The clay emerges as a continuous brick shaped column. Initially this is smooth but it can be modified by removing a thin sliver from the top and sides using a taught wire to produce a \u2018wiredrag\u2019 effect or by placing textured rollers over the column to create a rusticated. The clay column is then cut into single bricks and be loaded onto kiln cars.');

var _ref8 = _jsx('p', {}, void 0, 'These extruded bricks are generally perforated and can be solid but cannot be frogged.');

var _ref9 = _jsx('h2', {}, void 0, 'Drying');

var _ref10 = _jsx('p', {}, void 0, 'Before the bricks can be fired, as much moisture as possible must be removed or they will explode in the kilns. Drying involves the removal of water from the wet brick in such a way as to dry them out evenly from inside out. If the outer skin of the brick dries first it becomes impossible for the moisture inside to escape. In the kiln the extreme temperatures will force out this moisture and some cracking may occur. To prevent this happening the dryers are kept at temperatures of about 80 \u2013120 degrees centigrade and the atmosphere is very humid keeping the exterior of the brick as moist as possible. This is monitored very closely to reduce surface cracking. The bricks will shrink in the dryers as the clay particles come together and they become strong enough to be stacked, but at this stage they have no weather resistant qualities.');

var _ref11 = _jsx('p', {}, void 0, 'Drying schedules vary but between 18 to 40 hours is typical for an automated plant. Special shapes and large units can take up to a week or more. The dry bricks are then set onto kiln cars ready to be fired.');

